.doctor {
  padding: 10px;
  display: flex;
  position: relative;
  min-width: 255px;
  max-width: 255px;
  height: 390px;
  background-color: var(--white);
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  align-items: center;
  animation: fadeIn ease 0.5s;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.doctor__price {
  color: var(--gray);
  font-size: 13px;
  line-height: 130%;
  font-weight: 400;
}

.doctor .GetAvatar {
  margin-right: 0px;
}

.doctor__specialty {

  font-size: 16px;
  line-height: 130%;
  font-weight: 700;
}

.doctor__residence {

  text-align: center;
  color: var(--gray);
  font-size: 16px;
  line-height: 130%;
  font-weight: 400;
}

.doctor_logo {
  position: absolute;
  top: 25px;
  left: 20px;
}

.doctor__name {
  margin-right: 25%;
  margin-left: 25%;
  font-weight: 700;
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.08em;
}

@media (max-width: 900px) {
  .doctor .GetAvatar {
    height: 90px;
    width: 90px;
  }
}

@media (max-width: 650px) {
  .doctor {
    gap: 10px;
    min-width: 220px;
    max-width: 220px;

    height: 300px;
  }

  .doctor .GetAvatar {
    height: 65px;
    width: 65px;
  }

  .doctor_logo {
    left: 10px;
  }

  .doctor__name {
    font-size: 16px;
  }

  .doctor__residence {
    font-size: 11px;
  }

  .doctor__specialty {
    font-size: 14px;
  }

  .doctor__price {
    font-size: 10px;
  }

  .doctor__buttonWrapper {
    width: 90%;
  }
}

@media (max-width: 530px) {
  .doctor {
    gap: 10px;
    min-width: 190px;
    max-width: 190px;
  }
}