.Reservation {
	position: relative;
	display: flex;
	margin-right: auto;
	justify-content: center;
	margin-left: auto;
	background: var(--main-gradient);
	/* height: 90vw; */
	overflow: scroll;
	align-items: center;
	margin-top: 5px;
	width: 60vw;
	border-radius: 25px;
	height: 99vh;
	/* width: 50%; */
}

.Reservation__close {
	position: absolute;
	right: 30px;
	height: 30px;
	width: 30px;
	top: 20px;
}