.doctors {
    margin-top: 30px;
    display: flex;
    overflow-x: scroll;
    width: 100vw;
    gap: 10px;
    animation: fade-in-out 0.7s ease-in-out;

}

@keyframes fade-in-out {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

::-webkit-scrollbar {
    display: none;
}